.layout {
  margin: 0;
  font-size: 4rem;
  line-height: 1.2;
}
.layout .text {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 60%;
  font-weight: 300;
}
.layout .block {
  position: fixed;
  width: 50vw;
  height: 50px;
  bottom: 0px;
  left: 50%;
  transform: translateX(-50%);
  background-color: #DEEAF3;
}
